<template>
  <div>
    <section class="add-index-bar" v-show="showAddBar">
      <img src="~images/logo144.png" class="logo" alt="" />
      <div class="label">{{ $t("addFsv") }}</div>
      <button class="btn" type="button" @click="showModel">
        {{ $t("add") }}
      </button>
      <div class="close" @click="hideAddBar"></div>
    </section>
    <section class="model" v-if="modelVisible">
      <div class="close" @click="hideModel"></div>
      <div class="tip" v-if="!isIos">* {{ $t("chrome differences") }}</div>
      <div class="swiper-container">
        <div class="swiper-wrapper">
          <div
            class="swiper-slide"
            v-for="(item, index) in swiperList"
            :key="index"
          >
            <img class="img" :src="item.logo" alt="" />
            <div class="name" :key="item.name">{{ item.name }}</div>
          </div>
        </div>
        <div class="swiper-pagination"></div>
      </div>
    </section>
    <div class="model-shaow" v-if="modelVisible" @click="hideModel"></div>
  </div>
</template>
<script>
if ("standalone" in window.navigator && window.navigator.standalone) {
  var noddy,
    remotes = false;
  document.addEventListener(
    "click",
    function (event) {
      noddy = event.target;
      while (noddy.nodeName !== "A" && noddy.nodeName !== "HTML")
        noddy = noddy.parentNode;
      if (
        "href" in noddy &&
        noddy.href.indexOf("http") !== -1 &&
        (noddy.href.indexOf(document.location.host) !== -1 || remotes)
      ) {
        event.preventDefault();
        document.location.href = noddy.href;
      }
    },
    false
  );
}
import Swiper from "swiper"; //  ES6 导入swiper js文件
import "swiper/dist/css/swiper.min.css"; // ES6 导入swiper css文件
export default {
  props: ["lang"],
  data() {
    return {
      showAddBar: true,
      swiperList: [],
      modelVisible: false,
      isIos: false,
      swiper: null,
    };
  },
  computed: {},
  watch: {},
  created() {},
  methods: {
    hideAddBar() {
      this.showAddBar = false;
    },
    showModel() {
      let list = [];
      let isIos = /(iPhone|iPad|iPod|iOS)/i.test(navigator.userAgent);
      let isInWechat = window.navigator.userAgent
        .toLowerCase()
        .match("micromessenger");
      this.isIos = isIos;
      if (isIos) {
        console.log(this.lang);
        if (this.lang === "en") {
          list = [
            {
              logo: require("@/assets/images/ios_en/ios1.png"),
              name: this.$t("bottom popup"),
            },
            {
              logo: require("@/assets/images/ios_en/ios2.png"),
              name: this.$t("home screen"),
            },
            {
              logo: require("@/assets/images/ios_en/ios3.png"),
              name: this.$t("click to add"),
            },
            {
              logo: require("@/assets/images/ios_en/ios4.png"),
              name: this.$t("one-click"),
            },
          ];
        } else {
          list = [
            {
              logo: require("@/assets/images/ios/ios1.png"),
              name: this.$t("bottom popup"),
            },
            {
              logo: require("@/assets/images/ios/ios2.png"),
              name: this.$t("home screen"),
            },
            {
              logo: require("@/assets/images/ios/ios3.png"),
              name: this.$t("click to add"),
            },
            {
              logo: require("@/assets/images/ios/ios4.png"),
              name: this.$t("one-click"),
            },
          ];
        }
      } else {
        if (this.lang === "en") {
          list = [
            {
              logo: require("@/assets/images/and_en/android1.png"),
              name: this.$t("bottom popup"),
            },
            {
              logo: require("@/assets/images/and_en/android2.png"),
              name: this.$t("home screen"),
            },
            {
              logo: require("@/assets/images/and_en/android3.png"),
              name: this.$t("click to add"),
            },
            {
              logo: require("@/assets/images/and_en/android4.png"),
              name: this.$t("one-click"),
            },
          ];
        } else {
          list = [
            {
              logo: require("@/assets/images/and/android1.png"),
              name: this.$t("bottom popup"),
            },
            {
              logo: require("@/assets/images/and/android2.png"),
              name: this.$t("home screen"),
            },
            {
              logo: require("@/assets/images/and/android3.png"),
              name: this.$t("click to add"),
            },
            {
              logo: require("@/assets/images/and/android4.png"),
              name: this.$t("one-click"),
            },
          ];
        }
      }
      if (isInWechat) {
        if (this.lang === "en") {
          list.unshift({
            logo: require("@/assets/images/weixin_en.png"),
            name: this.$t("open in browser"),
          });
        } else {
          list.unshift({
            logo: require("@/assets/images/weixin.png"),
            name: this.$t("open in browser"),
          });
        }
      }
      this.swiperList = list;

      this.modelVisible = true;
      this.$nextTick(function () {
        new Swiper(".swiper-container", {
          pagination: {
            el: ".swiper-pagination",
            clickable: true,
          },
        });
      });
    },
    hideModel() {
      this.modelVisible = false;
    },
  },
  components: {},
};
</script>
<style lang="less" scoped>
.add-index-bar {
  position: fixed;
  left: 30px;
  top: auto;
  right: 30px;
  bottom: 24px;
  z-index: 1010;
  height: 110px;
  background: rgba(0, 0, 0, 0.7);
  border-radius: 20px;
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -webkit-align-items: center;
  -ms-flex-align: center;
  align-items: center;
  padding: 0 48px 0 20px;
}
.add-index-bar .logo {
  width: 70px;
  height: 70px;
  /* background: #13C09D; */
  border-radius: 16px;
}
.add-index-bar .label {
  -webkit-box-flex: 1;
  -webkit-flex: 1;
  -ms-flex: 1;
  flex: 1;
  min-width: 0;
  padding: 0 20px;
  font-size: 30px;
  font-weight: 500;
  color: #f9f9f9;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  line-height: 34px;
}
.add-index-bar .btn {
  width: 108px;
  height: 50px;
  background: #13c09d;
  border-radius: 12px;
  border: none;
  outline: none;
  font-size: 24px;
  font-weight: 500;
  color: #f9f9f9;
}
.add-index-bar .close {
  position: absolute;
  top: 0;
  right: 0;
  width: 40px;
  height: 40px;
  background: url(~images/close.png) no-repeat 20% 60%;
  background-size: 40%;
}
.model {
  position: fixed;
  top: 15%;
  left: 3%;
  z-index: 3000;
  width: 94%;
  border-radius: 12px;
  background: #fff;
  padding: 68px 48px 44px;
  /* box-sizing: border-box; */
}
.model .close {
  position: absolute;
  top: 48px;
  right: 40px;
  width: 42px;
  height: 42px;
  background: url(~images/close.png) no-repeat 50% 50%;
  background-size: 50% 50%;
  z-index: 10;
}
.swiper-wrapper {
  width: 100%;
  height: 100%;
}
.model .swiper-slide {
  padding: 40px 0 100px 0;
  text-align: center;
}
.model .name {
  color: #16221f;
  font-size: 32px;
  margin-top: 64px;
}
.model .img {
  display: block;
  width: 426px;
  height: 400px;
  margin: 0 auto;
}
.model .swiper-pagination-bullet-active {
  background: #13c09d;
}
.model .tip {
  font-size: 24px;
  color: #999;
}
.model-shaow {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 2000;
  background: rgba(0, 0, 0, 0.5);
}
</style>
